import React, { useState } from 'react'

const TextForm = (props) => {
    const [text, setText] = useState('Enter text here');
    //setText('new text');


    const handleUpClick = () =>{
        //console.log("uppercase was clicked");
        let newText=text.toUpperCase();
        setText(newText);
        props.showAlert("Converted to uppercase", "success");
    }

    const handleLoClick = () =>{
        //console.log("uppercase was clicked");
        let newText=text.toLowerCase();
        setText(newText);
        props.showAlert("Converted to lowercase", "success");
    }

    const handleOnChange = (event) =>{
        //console.log("on change");
        setText(event.target.value);
    }

    const handleCopy =()=>{
        navigator.clipboard.writeText(text);
        document.getSelection().removeAllRanges();
        props.showAlert("Text copied", "success");
    }

    const handleExtraSpaces = () => {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "));
        props.showAlert("Removed extra spaces", "success");
    }

    const handleClearText = () =>{
        let newText='';
        setText(newText);
        props.showAlert("Text cleared", "success");
    }


  return (
    <>
    <div className='container' style={{color: props.Mode==='light'?'grey':'white'}}>
        <h1>{props.heading}</h1>
        <div className="mb-3">
            <textarea className="form-control" style={{backgroundColor: props.Mode==='light'?'white':'#13466e', 
            color: props.Mode==='light'?'black':'white'}} value={text} onChange={handleOnChange} id="myBox" rows="8"></textarea>
        </div>
        <button disabled={text.length===0} className='btn btn-primary mx-1 my-1' onClick={handleUpClick}>Convert to uppercase</button>
        <button disabled={text.length===0} className='btn btn-primary mx-1 my-1' onClick={handleLoClick}>Convert to lowercase</button>
        <button disabled={text.length===0} className='btn btn-primary mx-1 my-1' onClick={handleCopy}>Copy Text</button>
        <button disabled={text.length===0} className='btn btn-primary mx-1 my-1' onClick={handleExtraSpaces}>Remove extra spaces</button>
    </div>
    <button disabled={text.length===0} className='btn btn-info mx-3 my-1' onClick={handleClearText}>Clear Text</button>
    <div className='container my-3' style={{color: props.Mode==='light'?'grey':'white'}}>
        <h2>Your text summary</h2>
        <p>{text.split(/\s+/).filter((element)=>{return element.length!==0}).length} words and {text.length} character</p>
        <h3>Preview</h3>
        <p>{text.length>0?text:"Nothing to preview"}</p>
    </div>
    </>
  )
}

export default TextForm
